.contact {

  @media (min-width: 720px) {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr;
  }

  &__element {
    margin-bottom: 30px;
  }

  &__title {
    color: var(--main-dark, #161616);
    font-family: Cinzel, sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 27px; /* 168.75% */
    padding-bottom: 1rem;
  }

  &__phone-caption,
  &__adress-caption {
    color: var(--main-dark, #161616);
    font-family: Cinzel, sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 27px; /* 192.857% */
  }

  &__phone-number,
  &__adress-street {
    color: var(--text, #3e3e3e);
    font-family: Cinzel, sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 140%; /* 22.4px */
    padding-bottom: 8px;
    padding-right: 40px;
    text-decoration: none;
  }
}
