.navigation {
  position: absolute;
  right: 20px;
  top: 36px;

  @media (min-width: 720px) {
    right: 72px;
  }

  &__phone-link {
    display: inline-block;
    width: 24px;
    margin-right: 24px;

    &::before {
      content: "The Met Fifth Avenue +1 212-535-7710";
      display: none;
      position: absolute;
      width: 210px;
      right: 150px;
      top: -15px;
      text-align: right;
      cursor: default;
    }

    &::after {
      content: "The Met Cloisters +1 212-923-3700";
      display: none;
      position: absolute;
      width: 180px;
      right: 150px;
      text-align: right;
      cursor: default;
    }

    &:hover {
      &::before {
        display: inline-block;
      }

      &::after {
        display: inline-block;
      }
    }

    &:focus-within {
      &::before {
        display: inline-block;
      }

      &::after {
        display: inline-block;
      }
    }

    @media (min-width: 720px) {
      margin-right: 50px;
    }
  }

  &__burger-menu {
    display: inline-block;
    width: 24px;

    @media (min-width: 720px) {
      width: 32px;
    }
  }

  &__phone-icon {
    @media (min-width: 720px) {
      width: 32px;
    }
  }

  &__burger-icon {
    @media (min-width: 720px) {
      width: 32px;
    }
  }
}
